<template lang="html">
  <div class="">
    <div style="height: 300px">
      <div ref="editor">
      </div>
    </div>
    <transition
      enter-active-class="animated animated-fast slideInUp"
      leave-active-class="animated animated-fast slideOutDown"
    >
      <div class="modal is-active" v-if="isActive">
        <div class="modal-background" @click="isActive = false"></div>
        <div class="modal-content box">
          <div class="tabs">
            <ul>
              <li :class="{'is-active': choose === 'url'}" @click="choose = 'url'"><a>Url</a></li>
              <li :class="{'is-active': choose === 'upload'}" @click="choose = 'upload'"><a>Upload</a></li>
            </ul>
          </div>
          <div v-if="choose === 'url'">
            <h4 class="label">Paste Url</h4>
            <p class="control">
              <input type="text" name="" value="" class="input" v-model="url">
            </p>
          </div>
          <div v-if="choose === 'upload'">
            <h4 class="label">Upload Image</h4>
            <p class="control">
              <input type="file" ref="file" name="" value="" class="">
            </p>
          </div>
          <br>
          <button class="button is-primary"
            name="button" @click="submit"
            :class="{'is-loading': isLoading}"
          > Ok </button>
          <button class="button" name="button" @click="isActive = false"> Cancel </button>
        </div>
        <button class="modal-close" @click="isActive = false"></button>
      </div>
    </transition>

  </div>
</template>

<script>

import Quill from 'quill'
import Vue from 'vue'

export default {
  props: {
    initContent: {
      default: ''
    }
  },
  data () {
    return {
      editor: null,
      choose: 'upload',
      isActive: false,
      url: '',
      isLoading: false
    }
  },
  methods: {
    submit () {
      let index = 0
      if (this.editor.getSelection()) {
        index = this.editor.getSelection().index
      }
      if (this.choose === 'url') {
        this.editor.insertEmbed(index, 'image', this.url)
        this.isActive = false
      } else if (this.choose === 'upload') {
        this.isLoading = true
        const data = new window.FormData()
        data.append('file', this.$refs.file.files[0])
        this.$store.dispatch('submitImage', { data })
          .then(path => {
            this.editor.insertEmbed(index, 'image', path)
            this.isActive = false
            this.isLoading = false
          })
      }
    }
  },
  mounted () { // 注意用 mounted 而不是 created
    var toolbarOptions = [
      ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
      // ['image'],
      ['blockquote', 'code-block'],
      // [{ 'header': 1 }, { 'header': 2 }],               // custom button values
      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
      [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
      [{ 'direction': 'rtl' }],                         // text direction
      // [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
      [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
      [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
      [{ 'font': [] }],
      [{ 'align': [] }],
      ['clean'],                                        // remove formatting button
      // ['formula'],
      ['image']
    ]
    let self = this
    function imageHandler (image) {
      self.isActive = true
    }
    this.editor = new Quill(this.$refs.editor, {
      theme: 'snow',
      modules: {
        // formula: true,
        toolbar: toolbarOptions
      }
    })
    Vue.nextTick(() => {
      this.editor.root.innerHTML = this.initContent
    })
    let toolbar = this.editor.getModule('toolbar')
    toolbar.addHandler('image', imageHandler)
    this.editor.on('text-change', function () {
      if (self.editor.root) {
        self.$emit('change', self.editor.root.innerHTML)
      }
    })
  },
  watch: {
    initContent (to, from) {
      if (to) {
        this.editor.root.innerHTML = to
      }
    }
  }
}
</script>

